@font-face{
  font-family: "holla";
  src: url('../fonts/HollaHearthDemoRegular.ttf')
}

html * {
  font-family: "holla";
  font-size: 40px;
}

.container{
  width: 400px;
  height: 400px;
  border: 2px solid black;
  overflow-y: hidden;
  position: relative;
}
.container:hover:before{
  content: '';
  height: 100%;
  background-color: black;
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0.3;
  border-radius: 50% 0;
  cursor: pointer;
}
.next{
  width: 600px;
  height: 200px;
  background-color: chocolate;
}